* {
  margin: 0;
  padding: 0; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

nav ul {
  text-align: right;
  position: fixed;
  width: 100%;
  background-color: #282c34; }
  nav ul li {
    display: inline-block;
    font-weight: bold;
    margin: 15px 25px; }
    nav ul li a {
      color: #f2f2f2;
      text-decoration: none; }
    nav ul li a:hover {
      color: #2fc02f; }

.intro {
  top: 0;
  min-height: 80vh;
  padding: 21vh 0 15vh 0;
  background: #282c34;
  color: #e4f1fe; }
  .intro h1 {
    font-size: calc(24px + 5vmin); }
  .intro h2 {
    text-align: center;
    font-size: 35px;
    font-weight: 200;
    margin: 1em 2.5em; }
    .intro h2 a {
      color: #008080;
      text-decoration: none; }
  .intro p a:hover {
    color: #2fc02f; }

samp {
  margin: 2em 1.5em;
  font-size: calc(10px + 2vmin); }

.aboutme {
  top: 0;
  min-height: 80vh;
  padding-top: 12vh; }
  .aboutme p {
    padding-top: 4px;
    color: #000000;
    text-align: center; }
  .aboutme h1 {
    font-size: 29px;
    text-align: center; }

.english {
  margin: 1.5em 0 2.5em;
  font-size: 14px; }

.whatToDo {
  margin: 1.5em 0; }

footer {
  padding: 20px;
  background: #22313F;
  color: white;
  line-height: 40px; }
  footer a {
    color: white;
    padding: 0 5px; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  height: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

